.nav{width:100%;height:60px;display:flex;flex-direction:row;justify-content:right;align-items:center;background-color:#040d16;overflow-x:hidden}.nav .back{border:0;background:transparent;float:left}.nav form{display:contents;width:100%}.nav input[type=text]{float:right;color:#fff;box-sizing:border-box;border:2px solid #fff;border-right:0;border-radius:10px;border-top-right-radius:0;border-bottom-right-radius:0;width:130px;font-size:1.5em;background-color:transparent;padding:5px;transition:width ease-in-out .75s;outline:none;margin-right:0}.nav input[type=text]:focus{width:98%}.nav .logo{fill:#fff}.nav svg{position:relative;margin-top:10px;width:2.5em;padding:5px;cursor:pointer;z-index:10}.nav h1{color:#fff;font-family:"Quicksand",sans-serif;font-weight:100;width:100%;text-align:center}@-webkit-keyframes delay-overflow{from{overflow-x:hidden}100%{overflow-x:hidden}}@keyframes delay-overflow{from{overflow-x:hidden}100%{overflow-x:hidden}}

