;left:0;right:0;bottom:0;background-color:#ddd;transition:.4s}.card-form .notes .switch .slider:before{position:absolute;content:"";height:26px;width:26px;left:4px;bottom:4px;background-color:#fff;transition:.4s}.card-form .notes .switch .slider.round{border-radius:34px}.card-form .notes .switch .slider.round:before{border-radius:50%}.card-form .tags{background-color:#fff;box-shadow:2px 2px 3px 3px #eee;padding:5px;margin-top:5px;width:96%;border-radius:10px;margin-bottom:20px}.card-form .tags .add-tag{font-family:"Quicksand",sans-serif;font-size:1.5em;border:none;border-radius:10px;background-color:transparent;background-color:#fff;box-shadow:2px 2px 3px 3px #eee;margin-bottom:10px}.card-form .tags .tag{width:100%;display:flex;margin-top:5px}.card-form .tags .tag:first-child{margin-top:20px}.card-form .tags .tag:last-child{margin-bottom:10px}.card-form .tags .tag input{width:100%;font-family:"Quicksand",sans-serif;font-size:1.5em;border-left:0;border-right:0;border-top:0;border-bottom:1px solid #040d16}.card-form .tags .checkbox{--background: #fff;--border: #D1D6EE;--border-hover: #BBC1E1;--border-active: #00aa00;--tick: #fff;position:relative;display:flex}.card-form .tags .checkbox input,.card-form .tags .checkbox svg{width:32px;height:32px;display:block}.card-form .tags .checkbox input{-webkit-appearance:none;-moz-appearance:none;position:relative;outline:none;background:var(--background);border:none;margin:0;padding:0;cursor:pointer;border-radius:4px;transition:box-shadow .3s;box-shadow:inset 0 0 0 1px var(--border);box-shadow:inset 0 0 0 var(--s, 1px) var(--b, var(--border))}.card-form .tags .checkbox input:hover{--s: 2px;--b: var(--border-hover)}.card-form .tags .checkbox input.checked{--b: var(--border-active)}.card-form .tags .checkbox svg{pointer-events:none;fill:none;stroke-width:2px;stroke-linecap:round;stroke-linejoin:round;stroke:var(--border-active);stroke:var(--stroke, var(--border-active));position:absolute;top:0;left:0;width:32px;height:32px;transform:scale(1) translateZ(0);transform:scale(var(--scale, 1)) translateZ(0)}.card-form .tags .checkbox.path input.checked{--s: 4px;transition-delay:.4s}.card-form .tags .checkbox.path input.checked+svg{--a: 16.1 86.12;--o: 102.22}.card-form .tags .checkbox.path svg{stroke-dasharray:86.12;stroke-dasharray:var(--a, 86.12);stroke-dashoffset:86.12;stroke-dashoffset:var(--o, 86.12);transition:stroke-dasharray .6s, stroke-dashoffset .6s}.card-form .tags .checkbox.bounce{--stroke: var(--tick)}.card-form .tags .checkbox.bounce input.checked{--s: 22px}.card-form .tags .checkbox.bounce input.checked+svg{-webkit-animation:bounce .4s linear forwards .2s;animation:bounce .4s linear forwards .2s}.card-form .tags .checkbox.bounce svg{--scale: 0}@-webkit-keyframes bounce{50%{transform:scale(1.2)}75%{transform:scale(0.9)}100%{transform:scale(1)}}@keyframes bounce{50%{transform:scale(1.2)}75%{transform:scale(0.9)}100%{transform:scale(1)}}

